import React from 'react'
import './BannerAll.css'
import 'aos/dist/aos.css'
const BannerAll = ({paragraphText,titleMaina,gifSpace}) => {
  return (
    <div style={{position:'relative'}}>
    <div className='banner-main-div'>
        <div className="row-banenr-all">
            <div className="colm-banner" data-aos ="fade-up"><div className="text-banner"><h4 className="banner-h4"  id='banner-h4' > 
{titleMaina}
</h4>
              <p className="banner-paragraph">
                {/* Our brand analysts guarantee that you will receive impeccable design and development solutions that best meet your branding needs at the best pricing. */}
{paragraphText}                </p></div></div>
            <div className="colm-banner" data-aos ="fade-up"><div className="image-banner" ><img src={gifSpace} alt="bannerimg" width="100%" /></div></div>
        </div>
    </div>
    </div>
  )
}

export default BannerAll